<template>
    <div class="bs-home">
        <homeHeader></homeHeader>
        <div class="bs-home-main">
            <homeLeftNav></homeLeftNav>
            <router-view @senddata='modifyData' :key="nowDate"></router-view>
            <!-- <managerModal></managerModal> -->
            <userModal v-if="isUserModal" :userData='userData' @close='closeUserModal'></userModal>
        </div>
    </div>
</template>

<script>
import homeHeader from '@/components/publicHeader'
import homeLeftNav from '@/components/Home/homeLeftNav'

export default {
    components:{
        homeHeader,
        homeLeftNav,
        managerModal : resolve => require(['@/components/Manager/managerModal'],resolve),
        userModal : resolve => require(['@/components/User/userModal'],resolve)
    },
    methods:{
        modifyData(data){
            this.isUserModal = true //显示用户模态层
            this.userData = data    //更改数据
        },
        closeUserModal(){
            this.isUserModal = false
            this.nowDate = new Date().getTime()
        }
    },
    data(){
        return{
            isUserModal:false,
            userData:{},
            nowDate:''
        }
    },
    watch:{
        userData(newval){
            this.userData = newval
        },
    }
}
</script>

<style lang='scss'>
    .bs-home{
        height: 100%;
        min-height: 100%;
        &-main{
            display: flex;
            height: 100%;
        }
    }
</style>
